.profile__name {
  margin: 0px;
  font-size: 42px;
  font-weight: 500;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 940px) {
  .profile__name {
    font-size: 27px;
    max-width: 240px;
    text-align: left;
  }
}