.menu__exit {
  margin: 0 auto;
  padding: 0;
  color: #A9A9A9;
  background-color: black;
  transition: 0.3s;
  border: 0;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}

.menu__exit:hover {
  opacity: 0.6;
  cursor: pointer;
}